.cart_container {
    background-color: rgb(100, 150, 150);
    width: 100vw;
    min-height: 26rem;
    height: auto;
    text-align: center;
    padding-bottom: 18rem;
}

.cart {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    
}   

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    padding: 0.2rem 1rem;
    margin: 0 1rem;
}

.total {
    background-color: rgb(100, 150, 150);
    width: 100vw;
    bottom: 9rem;
    position: fixed;
    padding: 1rem 0;
    text-align: center;
}

@media screen and (min-width: 810px) {
    .cart {
        min-height: 45rem;
        height: auto;
    }
    .total {
        bottom: 4rem;
    }
}

@media screen and (min-width: 1366px) {
    .cart {
        min-height: auto;
    }
    .total {
        bottom: 5rem;
    }
}