.home {
    width: 100vw;
    min-height: 41rem;
    background-color: rgb(100, 150, 150);
    text-align: center;
    padding-top: 5rem;
}

.home>button {
    padding: 0.5rem 0.5rem;
}

.flexing {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.flexing>img {
    width: 2.5rem;
    height: 2.5rem;
}

@media screen and (min-width: 810px) {
    .home {
        min-height: 48.5rem;
    }

    .flexing>img {
        width: 4rem;
        height: 4rem;
    }
}

@media screen and (min-width: 1366px) {
    .flexing>img {
        width: 5.625rem;
        height: 5.625rem;
    }
}