.nav {
    display: flex;
    background-color: rgb(45, 120, 120);
    width: 100vw;
    min-height: 4rem;
    bottom: 0;
    position: fixed;
    overflow: hidden;
    text-align: center;
    justify-content: space-evenly;
}

.nav_icon{
    transition: 0.1s;
    filter: invert(100%);
}

.nav_icon:hover {
    transform: scale(1.4);
    transition: 0.15s;
}

@media screen and (min-width: 481px){
    .nav {
        bottom: unset;
        position: unset;
    }
}
