.item_card_container {
    display: inline-block;
    background-color: rgb(130, 155, 155);
    width: 40%;
    height: 22rem;
    border: 0.0625rem solid black;
    border-radius: 0.1875rem;
    margin: 0.5rem 0.5rem;
    padding: 0.5rem;
    box-shadow: 0.125rem 0.2rem 0.25rem  rgb(59, 59, 59);
    transition: 0.1s;
}

.item_card_container:hover {
    filter: brightness(1.15);
    transform: scale(1.055);
    transition: 0.2s;
    box-shadow: none ;
}

.item_card_container>img {
    width: 3rem;
    height: 3rem;
}

.item_card_container>button {
    background-color: rgb(225, 210, 190);
    padding: 0.2rem 0.4rem;
    margin: 0.0625rem 0.2rem;
}

@media screen and (min-width: 810px){
    .item_card_container {
        width: 25%;
        height: 20rem;
    }

    .item_card_container>button {
        margin: 0 0.125rem;
    } 
}

@media screen and (min-width: 1366px){
    .item_card_container {
        width: 20%;
        height: 18rem;
    }

    .item_card_container>button {
        margin: 0 0.25rem;
    }
}

