.payment {
    background-color:rgb(100, 150, 150);
    text-align: center;
    width: 100vw;
    min-height: 36rem;
}

.checkout_list {
    text-align: center;
    
}

h3 {
    margin: 0;
}

.payment_mode {
    display: flex;
    flex-direction: column;
    text-align: center;
    line-height: 2;
    padding: 2rem;
}

.show_button {
    padding: 0.5rem;
    margin: 0 0.5rem;
}

@media screen and (min-width: 810px) {
    .payment {
        min-height: 54rem;
    }
}

@media screen and (min-width: 1366px) {
    .payment {
        min-height: 44rem;
    }
}

