.cart_card_container {
    display: inline-block;
    background-color: rgb(130, 155, 155);
    width: 40%;
    min-height: auto;
    height: 20rem;
    border: 0.0625rem solid black;
    border-radius: 0.1875rem;
    margin: 0.5rem 0.5rem;
    box-shadow: 0.125rem 0.2rem 0.25rem  rgb(59, 59, 59);
    line-height: 0.9;
}

img {
    width: 3rem;
    height: 3rem;
}

@media screen and (min-width: 810px){
    .cart_card_container {
        width: 25%;
        height: 20rem;
    }

    .cart_card_container>button {
        margin: 0 0.125rem;
    } 
}

@media screen and (min-width: 1366px){
    .cart_card_container {
        width: 20%;
        height: 23rem;
        line-height: 2rem;
    }

    .cart_card_container>button {
        margin: 0 0.25rem;
    }
}