.category {
    background-color: rgb(100, 150, 150);
    width: 100vw;
    min-height: auto;
    text-align: center;
    padding-bottom: 10rem;
}

.category_links {
    background-color: rgb(44, 88, 88);
    font-size: 1.3rem;
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
    text-decoration: none;
}

.category_content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    
}

.link {
    transition: 0.1s;
    filter: invert(100%);
}

.link:hover {
    transform: scale(1.4);
    transition: 0.1s;
}

@media screen and (min-width: 810px) {
    .category {
        min-height: 45rem;
        height: auto;
    }
}

@media screen and (min-width: 1366px) {
    .category {
        min-height: 34rem;
    }
}