.update_form {
    text-align: center;
    background-color: rgb(100, 150, 150);
    width: 100vw;
    min-height: 31rem;
    height: auto;
    padding-top: 5rem;
    line-height: 2rem;
}

@media screen and (min-width: 810px) {
    .update_form {
        min-height: 49rem;
        height: auto;
    }
}

@media screen and (min-width: 1366px) {
    .update_form {
        min-height: 28rem;
        height: auto;
    }
}