.footer {
    background-color: teal;
    width: 100vw;
    display: flex;
    height: 4rem;
    bottom: 5.2rem;
    align-items: center;
    justify-content: center;
    position: fixed;
}

@media screen and (min-width: 810px){
    .footer {
        display: flex;
        height: 4rem;
        align-items: center;
        justify-content: center;
        bottom: 0;
        position: fixed;
    }
}

@media screen and (min-width: 1366px){
    .footer {
        height: 5rem;
    }
}

